.postcard {
    border: 1px solid #d3d3d3;
    border-radius: 0.25rem;
}

.bg-img {
    background-size: cover;
    min-height: 180px;
    background-position: center;
    background-color: #ccc;
}

.cardContent {
    padding: 15px;
}

button {
    background-color: transparent;
    border: 1px solid #2196f3;
    padding: 0.5rem 1rem;
    cursor: pointer;
    border-radius: 0.25rem;
}